<template>
  <div>
	<el-row style="margin-bottom:20px;">
	    <el-card shadow="always">
	      <span class="content-title">新闻资讯管理</span>
			<el-button type="success" @click="openAddDialog()">新增</el-button>
			<el-button type="text" style="float: right; padding: 3px 0" @click="moreSelect"><i :class="selectClass"></i>更多选项</el-button>
			<div class="advanced-box" v-if="content_show">
				<el-form :inline="true" :model="formNews" class="demo-form-inline">
					<el-form-item label="新闻类型">
						<el-input v-model="formNews.news_type"></el-input>
					</el-form-item>
					<el-form-item label="关键字">
						<el-input v-model="formNews.key_word"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="onSubmit">查询</el-button>
					</el-form-item>
					<el-form-item>
						<el-button @click="reset">重置</el-button>
					</el-form-item>
				</el-form>
			</div>
	    </el-card>
	</el-row>
	<el-table
		border
		:data="tableData"
	>
		<el-table-column
			prop="index"
			label="序号"
			align="center"
			width="60">
		</el-table-column>
		<el-table-column
			prop="news_type"
			label="新闻类型"
			align="center"
			width="90">
		</el-table-column>
		<el-table-column
			prop="title"
			label="新闻标题"
			align="center">
		</el-table-column>
		<el-table-column
			prop="icon_src"
			label="新闻icon"
			align="center"
			width="110">
			<template slot-scope="scope">
		      <img  :src="scope.row.icon_src" alt="" style="width: 50px;height: 50px">
		    </template>
		</el-table-column>
		<el-table-column
			prop="is_top_name"
			label="新闻排序"
			align="center"
			width="100">
		</el-table-column>
		<el-table-column
			prop="status_name"
			label="发布状态"
			align="center"
			width="90">
		</el-table-column>
		<el-table-column
			prop="release_time"
			label="发布时间"
			align="center"
			width="160">
		</el-table-column>
		<el-table-column
			prop="update_user"
			label="修改者"
			align="center"
			width="110">
		</el-table-column>
		<el-table-column
			prop="update_time"
			label="修改时间"
			align="center"
			width="160">
		</el-table-column>
		<el-table-column
			prop="activity_id"
			label="活动id"
			align="center"
			width="80">
		</el-table-column>
		<el-table-column
			label="操作"
			align="center"
			width="210">
			<template slot-scope="scope">
		        <el-button
		          size="mini"
		          type="primary"
		          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
		        <el-button
		          size="mini"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		        <el-button
		          size="mini"
		          @click="handleUp(scope.$index, scope.row)">置顶</el-button>
		    </template>
		</el-table-column>
	</el-table>
	<div class="block pager">
	  <el-pagination
	    layout="prev, pager, next"
	    :total="50">
	  </el-pagination>
	</div>
	<el-dialog
	  title="新增"
	  :visible.sync="dialogVisible"
	  width="60%"
	  top="5vh"
	  :append-to-body='true'>
	  <div class="el-dialog-news">
	  <el-form label-position="top" :rules="rules" ref="formLabelAlign" label-width="80px" :model="formLabelAlign">
		  <el-form-item label="新闻标题" prop="title">
		    <el-input v-model="formLabelAlign.title"></el-input>
		  </el-form-item>
		  <el-form-item label="新闻icon图 图片建议尺寸:600×400" prop="ico_name">
		  	<el-col :span="20">
		    	<el-input v-model="formLabelAlign.ico_name" :disabled="true"></el-input>
		    </el-col>
		    <el-upload
			  class="upload-demo"
			  action="core/file/upload"
			  multiple
			  :limit="1"
			  :before-upload="beforeUpload"
			  :show-file-list='false'>
			  <el-button size="medium" type="primary">点击上传</el-button>
			</el-upload>
		  </el-form-item>
		  <el-form-item label="content" prop="content" style="margin-bottom:0;">
			  <!--<el-input
				  type="textarea"
				  v-model="formLabelAlign.content"
				  :autosize="{ minRows: 10, maxRows: 33}"
				  placeholder="请输入内容">
			  </el-input>-->
		  </el-form-item>
		  <Editor @postEditorToParent="getEditorContent" v-model="formLabelAlign.content"/>
		  <el-form-item label="关键字(用逗号隔开)">
		    <el-input v-model="formLabelAlign.key_words"></el-input>
		  </el-form-item>
		  <el-form-item label="新闻摘要">
		    <el-input v-model="formLabelAlign.sumamry"></el-input>
		  </el-form-item>
		  <el-form-item label="新闻来源">
		    <el-input v-model="formLabelAlign.source"></el-input>
		  </el-form-item>
		  <el-form-item label="发布状态">
		    <el-radio-group v-model="formLabelAlign.status">
		      <el-radio :label="0">未发布[预览]</el-radio>
		      <el-radio :label="1">已发布</el-radio>
		    </el-radio-group>
		  </el-form-item>
		  <el-form-item label="发布时间" prop="release_time">
		    <el-date-picker
		      v-model="formLabelAlign.release_time"
		      type="datetime"
		      value-format="yyyy-MM-dd HH:mm:ss"
		      placeholder="选择日期"
		      default-time="01:01:01">
		    </el-date-picker>
		  </el-form-item>
		  <el-row  :gutter="10">
		  	<el-col :span="10">
			  <el-form-item label="创建者" prop="author">
			    	<el-input v-model="formLabelAlign.author" :disabled="true"></el-input>
			  </el-form-item>
			</el-col>
			<el-col :span="10">
			  <el-form-item label="创建时间">
			    	<el-input v-model="formLabelAlign.create_time" :disabled="true"></el-input>
			  </el-form-item>
			</el-col>
		  </el-row>
		  <el-form-item label="源链接">
		    <el-input v-model="formLabelAlign.source_link"></el-input>
		  </el-form-item>
		  <el-form-item label="行业类型">
			  <el-select v-model="formLabelAlign.news_type" multiple placeholder="请选择" style="width:100%">
			    <el-option v-for="item in news_type_list" :key="item.name" :label="item.name" :value="item.name" ></el-option>
			  </el-select> 
		  </el-form-item>
	  </el-form>
	  </div>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="dialogVisible = false">取 消</el-button>
	    <el-button type="primary" @click="addNewsInfo('formLabelAlign')">确 定</el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import Editor from "../../editor"; //调用编辑器
import { authToken,authName } from '@/config';
import { storageHelper,getCurTime,utcTimeChange } from '@/utils';
export default {
	components: {
        Editor
    },
	data() {
		return {
			auth_name:'',
	        selectClass:"el-icon-arrow-down",
	        content_show:false,
	        formNews: {
	        	news_type: '',
	        	key_word: ''
	        },
	        //表格
	        tableData: [],
			//弹出框
			dialogVisible: false,
			//表单
			formLabelAlign: {
				title: '',
				content: '',
				sumamry: '',
				icon:'',
				ico_name:'',
				source: '',
				status: 0,
				news_type:[],
				key_words:'',
				release_time: '',
				author: '',
				create_time: '',
				source_link: '',
			},
			rules: {
				title: [{ required: true, message: '请输入活动名称', trigger: 'blur' }],
				content:[{ required: true, message: '请输入内容', trigger: 'blur' }],
				publish_time:[{ required: true, message: '请输入内容', trigger: 'blur' }],
				create_user:[{ required: true}],
				modify_user:[{ required: true}],
				modify_time:[{ required: true}],
			},
			news_type_list:[]
		};
    },
    mounted() {
    	this.auth_name = storageHelper.getItem(authName, 'healthcare');
    	this.getNewsList('1','15');
    	this.getdDictionaryInfo();
    },
    methods: {
    	openAddDialog(){
	      	this.formLabelAlign = {
	      		title: '',
				content: '',
				sumamry: '',
				icon:'',
				ico_name:'',
				source: '',
				status: 0,
				news_type:[],
				key_words:'',
				release_time: '',
				author: this.auth_name,
				create_time: getCurTime(),
				source_link: '',
	        };
	      	this.dialogVisible = true;
	    },
    	beforeUpload(file){
	      	var that =this;
	      	var fd = new window.FormData();
		    fd.append('file', file);
		    axios.post("core/file/upload", fd, {
		        headers: {
		        	'Content-Type':'multipart/form-data',
		            token: storageHelper.getItem(authToken, 'healthcare')
		        }
		    }).then(function (res) {
		    	if(res.code == 0){
		    		var url = res.data.url;
		    		that.formLabelAlign.ico_name = file.name;
		    		that.formLabelAlign.icon = res.data.code;
		    		that.$message({
			            type: 'success',
			            message: '上传成功！'
			        });
		    	}
		      //console.log(res);
		    });
		    return false // 返回false不会自动上传
	    },
		moreSelect(){
			if(this.selectClass=="el-icon-arrow-down"){
				this.selectClass = "el-icon-arrow-up";
				this.content_show = true;
			}else{
				this.selectClass = "el-icon-arrow-down";
				this.content_show = false;
			}
		},
		//查询
		onSubmit() {
			this.getNewsList("1","15");
		},
		//重置
		reset() {
			this.formNews = {
	        	news_type: '',
	        	key_word: ''
	        };
	        this.getNewsList("1","15");
		},
		//编辑
		handleEdit(index, row) {
	        this.dialogVisible = true;
	        var news_type = row.news_type.split(",");
	        this.formLabelAlign = {
	        	news_id: row.news_id,
	        	title: row.title,
				content: row.content,
				sumamry: row.sumamry,
				icon: row.icon,
				ico_name: row.ico_name,
				source: row.source,
				status: row.status,
				news_type: news_type,
				key_words: row.key_words,
				release_time: row.release_time,
				author: row.author,
				create_time: row.create_time,
				source_link: row.source_link
	        };
		},
		//删除
		handleDelete(index, row) {
			var that = this;
	      	this.$confirm('是否确认删除该条信息?', '提示', {
	          confirmButtonText: '确定',
	          cancelButtonText: '取消',
	          type: 'warning'
	        }).then(() => {
	        	var params = {news_id:row.news_id};
	        	that.submitNewsInfo('content/news/delete',params,'删除成功!');
	        }).catch(() => {
	        });
		},
		//置顶
		handleUp(index, row) {
			var params = {news_id:row.news_id};
	        this.submitNewsInfo('content/news/top',params,'置顶成功!');
	        console.log(row.news_id);
		},
		//编辑框提交按钮
		addNewsInfo(formType){
	      	var that = this;
	      	this.$refs[formType].validate(valid => {
		        if (valid) {
		        	that.dialogVisible = false;
		        	var method = '';
		        	var info = '';
		        	if(that.formLabelAlign.hasOwnProperty("news_id"))
		        	{
		        		method = 'content/news/update';
		        		info = '修改成功!';
		        	}else{
		        		method = 'content/news/create';
		        		info = '添加成功!';
		        	}
		        	that.formLabelAlign.news_type = that.formLabelAlign.news_type.join(',');
		        	that.submitNewsInfo(method,that.formLabelAlign,info);
		        } else {
		          return false;
		        }
		    });
	    },
	    getEditorContent(content){
	    	this.formLabelAlign.content = content;
	    	//console.log(content);
	    },
		getNewsList(page_index,page_size){
	      	var params = {news_type:this.formNews.news_type,key_word:this.formNews.key_word,page_index:page_index,page_size:page_size};
	      	var that = this;
			axios.get('content/news/pageList', {params}).then(function(response){
				var code = response.code;
				if(0 == code){
					var array = [];
					var rdata = response.data;
					var count = rdata.count;
					var page_index = rdata.page_index;
					var page_size = rdata.page_size;
					that.currentPage = page_index;
					that.page_size = page_size;
					that.total = count;
					var des_num = (page_index - 1) * page_size;
					var json = rdata.rows;
					for(var i = 0;i < json.length;i++){
						var is_top = json[i].is_top;
						var status = json[i].status;
						var icon_src = '';
						var ico_name = '';
						if(null != json[i].icon_info){
							icon_src = json[i].icon_info.src;
							ico_name = json[i].icon_info.name;
						}
						json[i].create_time = utcTimeChange(json[i].create_time);
						json[i].release_time = utcTimeChange(json[i].release_time);
						json[i].update_time = utcTimeChange(json[i].update_time);
						var is_top_name = "否";
						if(is_top == 1){
							is_top_name = "是";
						}
						var status_name = "未发布";
						if(status == 1){
							status_name = "已发布";
						}
						json[i].index = des_num + i + 1;
						json[i].is_top_name = is_top_name;
						json[i].status_name = status_name;
						json[i].icon_src = icon_src;
						json[i].ico_name = ico_name;
						json[i].activity_id = '';
						array.push(json[i]);
					}
					that.tableData = array;
				}else{
					//console.log(code);
				}
			},function(error){
				//console.log(error);
			});
	    },
	    submitNewsInfo(method,params,info){
		    var that = this;
			axios.post(method, params).then(function(response){
				var code = response.code;
				if(0 == code){
					that.$message({
			            type: 'success',
			            message: info
			        });
					that.getNewsList(that.currentPage,that.page_size);
				}else{
					that.$message({
			            type: 'success',
			            message: '操作失败:' + response.message
			        });
				}
			},function(error){
			});
	    },
	    getdDictionaryInfo(){
		    var that = this;
		    var params = {code:"news_type"}
			axios.get("core/dictionary/list", {params}).then(function(response){
				var code = response.code;
				if(0 == code){
					that.news_type_list = response.data;
				}else{
				}
			},function(error){
			});
	    }
    }
};
</script>

