.content-title{
	margin-right:20px;
}
.advanced-box{
	padding:20px 10px;
	margin-top:15px;
	background-color:#f7f7f7;
}
.pager{
	float:right;
	padding:10px 0;
}
.el-dialog__header{
	border-bottom: 1px solid #CCC;
}
.el-dialog-news{
	height:530px !important;
	padding-right:15px;
	overflow-y:scroll;
}
.el-dialog__footer{
	border-top: 1px solid #CCC;
}
